<template>
    <div id="translateS">
        <h1>在线翻译</h1>
        <h5 class="text-muted">简单 / 易用 / 便捷</h5>
        <div class="container">
            <form class="well form-inline" v-on:submit="formSubmit">
                <input class="form-control col-md-12 col-sm-12" type="text" v-model="translateText" placeholder="请输入要翻译的内容">
                <select class="custom-select" v-model="languge">
                    <option value="en">English</option>
                    <option value="ru">Russian</option>
                    <option value="ko">韩语</option>
                    <option value="ja">日语</option>
                </select>
                <input class="btn btn-priamry btn-outline-info" type="submit"  value="翻译">
            </form>
        </div>
        <h2 class="container bd-toc-link">{{translateTextOK}}</h2>
    </div>
</template>

<script>

import axios from 'axios'

export default{
    name:"translateS",
    data(){
        return{
            translateText:"",
            lang:"",
            translateTextOK:"",
            languge:""
        }
    },

    methods:{
        formSubmit(e){
            console.log(this.translateText);
            e.preventDefault();
            this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180314T151424Z.395878e88622ef04.122f6d10ca37b9df9b7901dfadf177149e47ad64&lang='+ this.languge +'&text=' + this.translateText)
            .then(res => {
                // console.log(res.body.text[0])
                this.translateTextOK = res.body.text[0];
            })
        }
    },

    created(){
        this.translateText = "你好";
        this.languge = "en";
    }
}
</script>

<style>
h1, h5 {
    text-align: center;
}

input[type='text'], select {
    width: 90%;
}


</style>




