<template>
  <div v-theme:column="'wide'" id="show-blogs">
    <h1>博客总览</h1>

    <!-- 搜索部分 -->
    <input type="text" v-model="search" placeholder="搜索" class="search">

    <div v-for="blog in filteredBlogs" :key="blog.id" class="single-blog">
        <!-- v-rainbow是自定义指令 -->
        <router-link :to="'/blog/' + blog.id">
            <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
        </router-link>
        <article>
            {{blog.content | snippet}}
        </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'show-blogs',
    data(){
        return {
          blogs:[],
          search:""
        }
    },

    created(){
        //请求数据
        // this.$http.get("https://wd8608360543qftkfs.wilddogio.com/posts.json")
        axios.get("/posts.json")
        .then(function(data){
            //当前return返回的就是下面的data.json()
            // console.log(data.data);
            return data.data;
        })
        //这个then是对当前return返回的data.json()进行再处理
        .then((data) => {
            var blogsArray = [];
            for(let key in data){
                // console.log(key);
                // console.log(data[key]);
                data[key].id = key;
                blogsArray.push(data[key]);
            }
            // console.log(blogsArray);
            console.log(this.blogs);
            this.blogs = blogsArray;
        })
    },

    computed:{
        //过滤器方法
        filteredBlogs(){
            return this.blogs.filter((blog) => {
                return blog.title.match(this.search);
            })
        }

    },

    //局部过滤方法
    filters:{
        // "to-uppercase":function(value){
        //     return value.toUpperCase();
        // }
        //另一种以方法表达的方式
        toUppercase(value){
            return value.toUpperCase();
        }
    },

    //本地自定义指令的方法
    // directives:{
    //     'rainbow':{
    //         bind(el,binding,vnode){
    //             el.style.color = 'red';
    //         }
    //     }
    // }
}
</script>

<style>
#show-blogs {
    max-width: 800px;
    margin: 0 auto;
}

.single-blog {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border-radius: 4px;
    border: 1px dotted #aaa;
}

.search {
    width: 98.5%;
    height: 30px;
    padding: 5px;
    border: 1px dotted #e5e6e6;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
}

#show-blogs a {
    text-decoration: none;
}

#show-blogs a:hover {
    text-decoration: underline;
    border-bottom: #ff0034;
}


</style>



